		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	.title>div {
		flex: 1;
		font-size: 0.22rem;
		border-right: 1px solid #F8FFFF;
		border-bottom: 1px solid #F8FFFF;
	}
	
	.titletop>div {
		font-size: 0.28rem;
		padding: 0.1rem 0;
		background: #507FCB;
	}
	
	.titledown>div {
		background: #2E405E;
		color: #F8FFFF;
		height: 0.45rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.changebt>span {
		border: 1px solid #2E405E;
		border-radius: 0.04rem;
		background: #507FCB;
		color: #F8FFFF;
		font-size: 0.18rem;
		padding: 0.04rem 0.14rem;
	}
	
	.otherparamdown {
		height: 3.68rem;
		overflow: scroll;
	}
	
	#paramset .surebutother {
		margin: 0.15rem auto 0;
	}
	
	.changemode {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		z-index: 11;
	}
	
	.changecont {
		width: 40%;
		padding: 0.4rem 0.3rem 0.2rem;
		background: #2E405E;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		border-radius: 0.1rem;
	}
	
	.changecont>div {
		border-radius: 0.1rem;
	}
	
	.caimg {
		width: 0.4rem;
		position: absolute;
		top: 0.1rem;
		right: 0.1rem;
	}
	/*重量系数*/
	
	.weightset {
		border: 0.02rem solid #F8FFFF;
		width: 96%;
		margin: 0.41rem auto;
		position: relative;
		padding: 0.4rem 0.1rem 0.3rem;
		box-sizing: border-box;
	}
	
	.weighttxt {
		position: absolute;
		top: -0.23rem;
		left: 0.14rem;
		font-size: 0.3rem;
		color: #F8FFFF;
		background: #2E405E;
		padding: 0 0.1rem;
	}
	
	.numlist {}
	
	.numlist>div {
		flex: 1;
		font-size: 0.16rem
	}
	
	.numlist>input {
		flex: 1;
		display: inline-block;
		width: 100%;
	}
	
	.weightcoefficient {
		width: 1.5rem;
		font-size: 0.26rem
	}
	
	.weightright {
		width: 100%;
	}
	
	.integral {
		margin-top: 0.1rem;
	}
	
	.weightset16 {
		margin-top: 0.2rem;
	}
	
	.surebutt {
		margin-top: 0.4rem;
		width: 1.2rem;
	}
	
	.setcontip {
		margin-top: 0.6rem;
		padding-bottom: 0.4rem;
	}
</style>


